<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('@/assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago > Depósito</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <div class="text text-center" style="display: block">Precio final: S/. {{ operation.price }}</div>
          </div>


          <div class="page-title">Depósito bancario</div>

          <div class="call-to-action">
            <v-img
                :src="require('@/assets/icons/lawyer-08.svg')"
                contain position="left"
                width="60"
            />
            <span>Ahorro soles:</span> 2345789- 03-124<br>
            <span>Titular:</span> De la cruz Rivera, R
          </div>

          <div class="call-to-action">
            <v-img
                :src="require('@/assets/icons/lawyer-09.svg')"
                contain position="left"
                width="60"
            />
            <span>Ahorro soles:</span> 2345789- 03-124<br>
            <span>Titular:</span> De la cruz Rivera, R
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn color="primary" width="160" @click="goToConfirm()">
              Continuar
              <v-img :src="require('@/assets/icons/lawyer-04.svg')" height="20" contain/>
            </v-btn>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'DocumentVariablePaymentVoucher',
  data: () => ({
    isLoading: false,
    operation: {
      status: {name: 'Loading...'},
      hasVoucher: false,
    },
    file: '',
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  mounted() {
    this.getOperation();
  },
  methods: {
    getOperation() {
      let url = 'operations/' + this.operationId
      axios.get(url).then((response) => {
        this.operation = response.data
      })
    },
    goToConfirm() {
      this.isLoading = true

      let url = 'operations/' + this.operationId;
      let data = {
        resource_id: this.operation.id,
        payment_method_id: 2,
        status_id: 1,
        coupon_code: this.operation.coupon_code,
      }

      axios.put(url, data).then((response) => {
        this.$router.push({name: 'OperationPayment', params: {id: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>

<style lang="scss" scoped>
.call-to-action {
  span {
    color: #184C72
  }
}
</style>